import { Component, OnInit } from '@angular/core';
import { Router } from '@angular/router';
import { Base } from '../../../model/base';
import { Inspecao } from './../../../model/objetc/inspecao';
import { Filtro } from '../../../model/objetc/filtro/filtro';
import { Pesquisa } from '../../../model/objetc/filtro/pesquisa';
import { InspecaoService } from './../../../service/sigobra/inspecao.service';
import { TipoinspecaoService } from './../../../service/sigobra/tipoinspecao.service';
import { StatusitensinspecaoService } from './../../../service/sigobra/statusitensinspecao.service';
import { InspecaoItemTipoInspecao } from 'src/app/model/objetc/inspecaoitemtipoinspecao';
import { TipoInspecao } from 'src/app/model/objetc/tipoinspecao';
import { Obras } from 'src/app/model/objetc/obras';
import { StatusItemInspecao } from 'src/app/model/objetc/statusiteminspecao';
import { ObraService } from './../../../service/sigobra/obra.service';
import { ItemInspesionadoService } from '../../../service/sigobra/item-inspesionado.service';
import { JoinType } from 'src/app/model/enums/join-type.enum';
import { Platform } from '@ionic/angular';

@Component({
  selector: 'app-tarefas',
  templateUrl: './tarefas.page.html',
  styleUrls: ['./tarefas.page.scss'],
})
export class TarefasPage implements OnInit {

  inspecaoitens: Array<InspecaoItemTipoInspecao>;
  obrasPesquisa: Array<Obras>;
  statusitens: Array<StatusItemInspecao>;
  tipoinspecoes: Array<TipoinspecaoService>;
  filtro = false;
  value: string = '';
  // dados em tela
  obrasRelatorio: Array<Obras>;
  inspecoes: Array<Inspecao>;
  // filtros
  filtros = {
    local: '',
    dtAbertura: '',
    dtFechamento: '',
  };
  statusPesquisa: StatusItemInspecao;
  obraPesquisa: Obras;
  tipoInspecaoPesquisa: TipoInspecao;

  tipoFechamento: any = { index: 0, nome: 'Não filtar' };
  opcoesTipoFechamento = [
    { index: 0, nome: 'Não filtar' },
    { index: 1, nome: 'Abertas' },
    { index: 2, nome: 'Fechadas' },
  ];
  // end filtros


  indexSelecionado: number = -1;
  multiplicador: number = 150;

  constructor(private base: Base, private is: InspecaoService,
    private tis: TipoinspecaoService,
    private siis: StatusitensinspecaoService,
    private os: ObraService,
    private itensinspesionados: ItemInspesionadoService,
    private router: Router,
    private plataforma: Platform) {
    this.plataforma.ready().then(
      (readsourcey) => {
        this.multiplicador = (this.plataforma.width() > 600) ? 150 : 200;
      }
    );
  }

  ngOnInit() {

    this.carregarUltimasObrasAbertas();
    this.carregarobras();
    this.carregartipoinsp();
    this.carregarstatus();
  }

  pesquisar() {
    this.carregarUltimasObrasAbertas();
  }

  realizar(inspencao: Inspecao) {
    this.is.inspecao = inspencao;
    this.router.navigate(['inspecao-realizar']);
  }

  carregarUltimasObrasAbertas() {
    this.base.present();
    this.os.findTop10ComInspencoesAbertas().subscribe(
      data => {
        this.base.dismiss();
        this.obrasRelatorio = data as Array<Obras>;
      }, error => {
        this.base.dismiss();
        this.base.mensagemErro('Falha ao consultar  Inspecao Itens' + this.base.tratarErro(error));
      }
    );
  }

  carregarstatus() {
    this.siis.pesquisar(" ").subscribe(
      data => {
        this.statusitens = data as Array<StatusItemInspecao>;
      }, error => {
      }
    );
  }

  carregartipoinsp() {
    this.tis.pesquisar(" ").subscribe(
      data => {
        this.tipoinspecoes = data as Array<TipoinspecaoService>;
      }, error => {
      }
    );
  }

  carregarobras() {
    this.os.pesquisar(" ").subscribe(
      data => {
        this.obrasPesquisa = data as Array<Obras>;
      }, error => {
      }
    );
  }

  habilitarFiltro() {
    this.filtro = !this.filtro;
  }

  filtar() {
    const filtro: Filtro = new Filtro();
    filtro.nativo = true;
    filtro.pesquisas = new Array<Pesquisa>();
    filtro.sql = 'select obr.* from obras obr inner join inspecao ins on ins.obras_id = obr.id where obr.id > 0';

    let pesquisa: Pesquisa = new Pesquisa();

    if (this.filtros.local !== undefined && this.filtros.local !== null && this.filtros.local !== '') {
      pesquisa = new Pesquisa();

      pesquisa.comparacao = 'like';
      pesquisa.nome = ' and  ins.local';
      pesquisa.valor = '\'%' + (this.filtros.local) + '%\'';
      filtro.pesquisas.push(pesquisa);
    }
    if (this.obraPesquisa !== undefined && this.obraPesquisa !== null) {
      pesquisa = new Pesquisa();
      pesquisa.comparacao = '=';
      pesquisa.nome = ' and obr.id';
      pesquisa.valor = this.obraPesquisa.id.toString();
      filtro.pesquisas.push(pesquisa);
    }
    if (this.tipoInspecaoPesquisa !== undefined && this.tipoInspecaoPesquisa !== null) {
      pesquisa = new Pesquisa();
      pesquisa.comparacao = '=';
      pesquisa.nome = ' and ins.tipo_inspecao_id';
      pesquisa.valor = this.tipoInspecaoPesquisa.id.toString();
      filtro.pesquisas.push(pesquisa);
    }

    switch (this.tipoFechamento.index) {
      case 1: {
        pesquisa = new Pesquisa();
        pesquisa.comparacao = 'is null';
        pesquisa.nome = ' and ins.dtfinalizada';
        pesquisa.valor = ' ';
        filtro.pesquisas.push(pesquisa);
        break;
      }
      case 2: {
        pesquisa = new Pesquisa();
        pesquisa.comparacao = 'is not null';
        pesquisa.nome = ' and ins.dtfinalizada';
        filtro.pesquisas.push(pesquisa);
        break;
      }
    }

    

    if (this.filtros.dtAbertura !== undefined && this.filtros.dtAbertura !== null && this.filtros.dtAbertura !== '') {
      pesquisa = new Pesquisa();
      pesquisa.comparacao = '>=';
      pesquisa.nome = ' and dtinicio';
      pesquisa.valor = this.filtros.dtAbertura;
      filtro.pesquisas.push(pesquisa);
    }
    if (this.filtros.dtFechamento !== undefined && this.filtros.dtFechamento !== null && this.filtros.dtFechamento !== '') {
      pesquisa = new Pesquisa();
      pesquisa.comparacao = '>=';
      pesquisa.nome = ' and dtFechamento';
      pesquisa.valor = this.filtros.dtFechamento;
      filtro.pesquisas.push(pesquisa);
    }
    this.indexSelecionado = -1;
    filtro.groupBy = ' group by obr.id';
    this.base.present();
    this.os.filtra(filtro).subscribe(
      data => {
        this.base.dismiss();
        this.obrasRelatorio = data as Array<Obras>;
      }, error => {
        this.base.dismiss();
        this.base.mensagemErro('Falha ao filtrar registros ' + this.base.tratarErro(error));
      }
    );
  }

  getNome(inspecaoitem: InspecaoItemTipoInspecao) {
    try {
      return inspecaoitem.statusInspecao.statusiteminspecao.nome;
    } catch (error) {
      return 'Item nao inspencisonado';
    }
  }

  selecionarIndex(obra: Obras, value: number) {


    if (obra.inspecoes === undefined || obra.inspecoes === null || obra.inspecoes.length <= 0) {
      const filtro: Filtro = new Filtro();
      filtro.pesquisas = new Array<Pesquisa>();

      let pesquisa = new Pesquisa();
      pesquisa.comparacao = '=';
      pesquisa.nome = 'id';
      pesquisa.tabela = 'obras';
      pesquisa.tipe = JoinType.INNER;
      pesquisa.valor = obra.id.toString();
      filtro.pesquisas.push(pesquisa);
      this.base.present();

      this.is.filtra(filtro).subscribe(
        data => {
          this.base.dismiss();
          obra.inspecoes = data as Array<Inspecao>;
        }, error => {
          this.base.dismiss();
          this.base.mensagemErro('Falha ao consultar inspecões ' + this.base.tratarErro(error));
        }
      );
    }
    if (this.indexSelecionado === value) {
      this.indexSelecionado = -1;
    } else {
      this.indexSelecionado = value;
    }
  }

  getTamanhoExpacao(obra: Obras): string {
    try {
      return (obra.inspecoes.length * this.multiplicador).toString() + 'px';
    } catch(error) {
      return (1 * this.multiplicador).toString() + 'px';
    }
    
  }

  getNomeIco(value: number): string {
    if (this.indexSelecionado === value) {
      return 'arrow-up-outline';
    } else {
      return 'arrow-down-outline';
    }
  }
}
